<template>
  <div id="dashboard">
    <h1>That's the dashboard!</h1>
    <p>You should only get here if you're authenticated!</p>
    <p> your email is : {{ email }} </p>
    <p> your age is : {{ age }} </p>
    <p ></p>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data () { 
    return {
      email: '',
      age: ''
    }
  },
  created () {
    axios.get('/users.json')
      .then(res => {
        console.log(res)
        const data = res.data
        const users = []
        for (let key in data) {
          const user = data[key]
          users.id = key 
          users.push(user)
        }
        console.log(users)
        this.email = users[0].email
        this.age = users[1].age
      })
      .catch(error => console.log(error));
  }
}
</script>

<style scoped>
  h1, p {
    text-align: center;
  }

  p {
    color: red;
  }
</style>


